
.loading {
  position: absolute!important;
  top: -7%!important;
  left: 7% !important;text-transform: capitalize;
  text-align: center;
  margin-top: 3rem;
}
.signUp {
  height: 23rem;
  width: 30vw;
  background-color:rgb(35, 35, 35);
  border-radius: 10px;
  position: fixed;
  z-index: 4;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  padding: 2rem;
  
  animation: EErow ease-in 0.3s; 
  &-closeBtn svg {
  color: gold;
  position: absolute;
  top: 0%;
  right: 0%;
}
&-text {
  margin-top: 1rem;
     & p {
        color: #ccc;
     }
     & a {
       color: whitesmoke !important;
       &:hover {
         cursor: pointer;
          color: white;
       }
     }
}
}


@media (max-width: 1048px) and ( max-height:2000px) {

 
  .signUp {
    height: 80vh!important;
    width: 100vw!important;
    overflow: scroll;
  }

}
@media (max-height: 500px) {
  .signUp {
    height: 80vh;
    width: 50vw;
  }

}


